
@import url(assets/kube-6.5.2/css/kube.css);
#wrapper {
  font-family: 'Open Sans', Helvetica, Arial, sans-serif;
  margin: auto;
  max-width: 1128px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 35px;
}
.head {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin-bottom: 10px;
}
.logo img {
  height: 128px;
}
.menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 1.3rem;
  font-weight: bold;
  margin-bottom: 20px;
}
.menu ul {
    margin: 0 auto;
    list-style: none;
}
.menu li {
    display: inline-block;
}
.menu li a {
      display: block;
      padding: 5px 10px;
}
a {
  text-decoration: none;
  color: #255bbd;
}
a:hover, a.active {
    color: #f03c69;
}
.logo {
  margin: 0 auto;
}



